@import "variables.scss";

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("//db.onlinewebfonts.com/t/5fc6f4fd75e944a52a342ab2c7f100f1.woff2") format("woff2"),
       url("//db.onlinewebfonts.com/t/5fc6f4fd75e944a52a342ab2c7f100f1.woff") format("woff");
}

@font-face {
  font-family: "Montserrat-Bold";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("//db.onlinewebfonts.com/t/07eae3dd22b2a99864228b51f4f918a5.woff2") format("woff2"),
       url("//db.onlinewebfonts.com/t/07eae3dd22b2a99864228b51f4f918a5.woff") format("woff");
}

body {
  padding: 0;
  min-width: 320px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #283645;
  overflow-x: hidden;
}

h2,
h3,
p {
  margin: 0;
}

img {
  margin: 0;
  padding: 0;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrap {
  width: 320px;
  margin: 0 auto;
  padding: 0 10px;
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    width: 767px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.btn {
  width: 320px;
  height: 58px;
  margin: 0 auto;
  background-color: #7eb252;
  color: #ffffff;
  font-family: "Montserrat-Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 22px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  outline: none;
  border: none;
}
